ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  border-radius: 12px 12px 0 0;
  width: 100%;
  height: 300px;
  display: block;
}

p {
  margin: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.gelery_items {
  border-radius: 22px;
  outline: 1px solid gray;
  flex-basis: calc(33.3333% - 20px);
  padding: 20px;
  overflow: hidden;
}

.stats {
  background-color: #12121041;
  border-radius: 0 0 12px 12px;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 20px;
  display: flex;
}

.stats-item {
  color: #fff;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  display: flex;
}

.btn-load {
  color: #fff;
  background-color: #12121041;
  border: none;
  border-radius: 12px;
  margin: 25px auto 50px;
  padding: 12px 22px;
  display: block;
}

input {
  border: none;
  border-radius: 12px;
  outline: 1px solid #12121041;
  width: 400px;
  height: 50px;
  margin: 25px auto;
  padding-left: 20px;
  display: block;
}
